const validationKirimMasak = (values) => {
  const errors = {};
  if (!values.kodeBarang) {
    errors.kodeBarang = "Tidak Boleh Kosong";
  }
  if (!values.kadarTransaksi) {
    errors.kadarTransaksi = "Tidak Boleh Kosong";
  }
  if (!values.bruto) {
    errors.bruto = "Tidak Boleh Kosong";
  }
  if (!values.netto) {
    errors.netto = "Tidak Boleh Kosong";
  }
};

export default validationKirimMasak;
