import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";
import { getListTukang } from "../../actions/listTukang";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const { SearchBar } = Search;
const columns = [
  {
    dataField: "kodeLokasi",
    text: "Kode Tukang",
  },
  {
    dataField: "namaLokasi",
    text: "Nama Tukang",
  },
];

const maptostate = (state) => {
  return {
    listTukang: state.provinsi.listTukang,
  };
};

class FormKirimMasak extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kode_tukang: null,
      nama_tukang: null,
      listTukang: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListTukang());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };
  render() {
    return (
      <div className="row">
        <form onSubmit={this.props.handleSubmit} className="col-lg-12">
          <div className="row">
            <div className="col-md-2">
              <div className="form-group">
                <Field
                  name="kodeLokasi"
                  label="Kode Tukang"
                  component={renderField}
                  type="text"
                />
              </div>
            </div>
            <div className="col-md-10">
              <div className="form-group">
                <Field
                  name="namaLokasi"
                  label="Nama Tukang"
                  component={renderField}
                  type="text"
                />
              </div>
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  name="submit"
                  id="simpan"
                  type="submit"
                  className="btn btn-info"
                >
                  Submit
                </button>
              </div>
            </div>
          </div>
        </form>
        <div className="col-lg-12 col-md-12">
          {this.props.listTukang ? (
            <ToolkitProvider
              keyField="id"
              data={this.props.listTukang}
              columns={columns}
              search
            >
              {(props) => (
                <div className="form-group">
                  <h3 className="mt-2">Kolom Pencarian</h3>
                  <div>
                    <SearchBar
                      {...props.searchProps}
                      className="form-control"
                      placeholder="Tuliskan Kode / Nama"
                      style={{ margin: 0, top: "50%" }}
                    />
                  </div>
                  <hr />
                  <BootstrapTable
                    {...props.baseProps}
                    striped
                    hover
                    condensed
                    bordered={true}
                    headerClasses="header-table"
                    pagination={paginationFactory()}
                  />
                </div>
              )}
            </ToolkitProvider>
          ) : (
            <Skeleton width={"100%"} height={300} />
          )}
        </div>
      </div>
    );
  }
}

FormKirimMasak = reduxForm({
  form: "FormKirimMasak",
  enableReinitialize: true,
})(FormKirimMasak);
export default connect(maptostate, null)(FormKirimMasak);
