import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Axios from "axios";
import validationKirimMasak from "../../Validations/validationKirimMasak";
import { getTimbangan } from "../../GetTimbangan";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormKirimMasakBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBarang: [],
    };
  }

  handleChange = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      kadarCetak: result_explode[1],
      kadarBeli: result_explode[2],
      namaJenis: result_explode[3],
    });
  };

  setKadar() {
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarBeli);
    this.props.change("keterangan", this.state.namaJenis);
  }
  setNetto() {
    this.props.change(
      "netto",
      this.props.netto === "NaN" ? 0 : this.props.netto
    );
  }

  componentDidMount() {
    Axios.get(server + "subcategorys2?kodeKelompok=PERHIASAN")
      .then((res) =>
        this.setState({
          listBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
    getTimbangan(this.props.change);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-3 col-lg-3">
            <label htmlFor="">Kode Model</label>
            <Field
              name="kodeBarang"
              component="select"
              className="form-control"
              onChange={this.handleChange()}
              onBlur={this.setKadar()}
            >
              <option value="DEFAULT|DEFAULT|DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.state.listBarang.map((list) => (
                <option
                  value={
                    list.kodeJenis +
                    "|" +
                    list.kadarCetak +
                    "|" +
                    list.kadarBeli +
                    "|" +
                    list.namaJenis
                  }
                  key={list.kodeJenis}
                >
                  {list.namaJenis}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="kadarTransaksi"
              label="Kode Tukar"
              component={renderField}
              type="text"
              onBlur={this.setNetto()}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="bruto"
              label="Bruto (Gr)"
              component={renderField}
              type="text"
              onBlur={this.setNetto()}
            />
          </div>
          <div className="col-md-3 col-lg-3">
            <Field
              name="netto"
              label="Netto (Gr)"
              component={renderField}
              type="text"
              onBlur={this.setNetto()}
              readOnly={true}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button
                className="btn btn-primary mb-3 mt-3"
                onClick={this.submit}
              >
                Tambah data
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKirimMasakBarang = reduxForm({
  form: "FormKirimMasakBarang",
  enableReinitialize: true,
  validate: validationKirimMasak,
})(FormKirimMasakBarang);
const selector = formValueSelector("FormKirimMasakBarang"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi } = selector(state, "bruto", "kadarTransaksi");
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
  };
})(FormKirimMasakBarang);
